import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
export default {
	data() {
		return {
			navBar: navBar,
			arrObj: {
				is_float: 0
			},
			is_miniBind: '',
			memberInfoData: '',
			code: ''
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			})
		},
		recordPage() {
			uni.navigateTo({
				url: './investlist'
			})
		},
		memberInfo() {
			var that = this
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				timeStamp: timeStamp,
				sign: sign,
			}
			rs.getRequests("memberInfo", data, (res) => {
				if (res.data.code == 200) {
					that.memberInfoData = res.data.data.info;
				}
			})
		},
		//充值
		toParent(e) {
			if(this.is_miniBind == 0){
				rs.Toast('请先绑定微信')
				setTimeout(function() {
					uni.switchTab({
						url: "/pages/tabar/user"
					})
				}, 1000);
				return;
			}
			var that = this;
			var price = e.val;
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				price: price,
				timeStamp: timeStamp,
			}







			     let type='mini';

			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				price: price,
				type: type,
				timeStamp: timeStamp,
				sign: sign,
			}
			rs.postRequests("createRecharge", data, (res) => {
				if (res.data.code == 200) {
					uni.navigateTo({
						url: './investrecord?orderId=' + res.data.data.orderId,
					})
				}else if (res.data.code == 404) {
					rs.Toast('未找到用户')
				}else if (res.data.code == 500) {
					rs.Toast('充值失败')
				}else{
					rs.Toast(res.data.msg)
				}
			})
		},
		
	},
	/**
	 * 生命周期函数--监听页面显示
	 */
	onShow: function() {
		var that = this;
		that.is_miniBind = uni.getStorageSync("is_miniBind"),
		that.memberInfo()
	},
};
